.main-info {

}

.main-info-wrapper {
  padding-top: 40px;
  padding-bottom: 60px;
}

.main-info__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  color: @black;
}

.main-info__content {
  display: flex;

  @media screen and (max-width: @medium) {
    flex-direction: column;
  }
}

.main-info__img-wrapper {
  padding-right: 52px;
  position: relative;

  @media screen and (max-width: @medium) {
    margin-bottom: 30px;
    padding-right: 0;
  }
}

.main-info__img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.main-info__bold-txt {
  font-size: 63px;
  line-height: 1;
  font-weight: 700;
  transform: rotate(270deg);
  background-color: transparent;
  display: block;
  position: absolute;
  bottom: 169px;
  right: -125px;
  color: @white;
  letter-spacing: 0.25rem;

  @media screen and (max-width: @medium) {
    font-size: 84px;
    line-height: 84px;
    bottom: 220px;
    right: -235px;
  }
}

.main-info__content-txt {
  justify-content: space-between;
  display: flex;
  flex-grow: 1;
  padding-right: 30px;
  margin-top: -8px;

  p {
    width: 258px;

    @media screen and (max-width: @medium) {
      width: 47%;
    }
  }
}

.main-info__more {
  display: block;
  margin-top: 32px;
  color: #214FA8;
  font-family: @mont;
  font-weight: 600;
  font-size: 17px;
  line-height: 21px;
}
