* {
  box-sizing: border-box;
}

body {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  font-family: @inter;
  color: @text-color;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

// img {
//   width: 100%;
//   height: auto;
// }

.container {
  width: 100%;
  max-width: 1280px;
  padding: 0 40px;
  margin: 0 auto;

  @media screen and (max-width: @medium) {
    padding: 0 20px;
  }
}

.vis-hidd {
  position: absolute !important;
  clip: rect(1px 1px 1px 1px); /* IE6, IE7 */
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0 !important;
  border: 0 !important;
  height: 1px !important;
  width: 1px !important;
  overflow: hidden;
}

a {
  text-decoration: none;
  transition: all ease-in 0.3s;

  &:active {
    opacity: 0.8;
  }
}

p {
  margin: 0;
}

