.slider--main {
  .slider__wrapper {
    height: 100%;
    background-color: #124239;

    .slick-dots {
      position: absolute;
      top: 40px;
      left: 20px;
      display: flex;
    }
  }

  .slider__item {
    height: 100%;
    background-image: url("../img/slider-item-bg.jpg");
    background-size: 895px 495px;
    background-position: 50%;
    background-repeat: no-repeat;
  }

  .slider__item-wrapper {
    height: 100%;
    padding-top: 176px;
    padding-left: 50px;
    padding-bottom: 87px;
    position: relative;
  }

  .slider__item-title {
    font-size: 40px;
    line-height: 1;
    margin-bottom: 27px;
    font-family: @mont;
    font-weight: 700;
    color: @white;
    width: 100%;
    max-width: 462px;
    margin-top: 0;
  }

  .slider__articul {
    font-size: 25px;
    line-height: 1;
    font-family: @mont;
    font-weight: 400;
    margin-bottom: 37px;
    color: @white;
  }

  .slider__item-link {
    font-size: 17px;
    line-height: 21px;
    font-family: @mont;
    font-weight: 600;
    color: @white;
  }

  .slider__img-wrapper {
    width: 380px;
    position: absolute;
    top: 0;
    right: 0;
    text-align: center;
    padding-right: 35px;
  }
}

.slider--partners {
  .slider__wrapper {
    background-color: @white;
    min-height: 218px!important;
  }

  .slider__item {
    min-height: 109px;
    display: flex;
    position: relative;
    width: 100%;
    // max-width: 590px;
    background-image: none;
    background-color: #F4F7FB;
    padding-top: 28.5px;
    padding-bottom: 28.5px;
    padding-left: 44px;
    padding-right: 240px;

    @media screen and (max-width: @medium) {
      max-width: 100%!important;
    }
  }

  .slick-active {
    margin-right: 20px;

    @media screen and (max-width: @medium) {
      margin-right: 0;
    }
  }

  .slider__bold {
    width: 100%;
    max-width: 280px;
    font-family: @mont;
    font-size: 20px;
    line-height: 26px;
    color: @black;
  }

  .slider__img {
    position: absolute;
    top: 0;
    right: 60px;
  }
}
