.main-news {
  background-color: @grey;
  padding-top: 47px;
  padding-bottom: 70px;
}

.main-news__wrapper {
  overflow-x: hidden;
}

.main-news__title {
  font-size: 30px;
  line-height: 30px;
  font-family: @mont;
  color: @black;
}

.main-news__list {
  display: flex;
  justify-content: space-between;
  margin-bottom: 15px;

  @media screen and (max-width: @medium) {
    width: 1200px;
    margin-left: -20px;
  }
}

.main-news__list-item {
  width: 100%;
  max-width: 285px;

  @media screen and (max-width: @medium) {
    margin-right: 20px;
  }
}

.main-news__img-wrapper {
  width: 100%;
  height: 224px;
  margin-bottom: 25px;
}

.main-news__img {
  width: 100%;
  height: auto;
  object-fit: cover;
}

.main-news__link {
  font-size: 20px;
  line-height: 24px;
  font-weight: 600;
  font-family: @mont;
  color: @black;
  display: block;
  margin-bottom: 20px;

  &:hover {
    color: #214FA8;
  }
}

.main-news__link-all {
  color: #214FA8;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  font-family: @mont;
}
