* {
  box-sizing: border-box;
}
body {
  padding: 0;
  margin: 0;
  font-weight: 400;
  font-size: 14px;
  line-height: 25px;
  font-family: "Inter", sans-serif;
  color: #868794;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
.container {
