.products {
  border: 1px solid #DCDDE6;

  .slick-arrow {
    width: 53px;
    height: 53px;
    border-radius: 50%;
    box-shadow: 0px 3px 10px 0px rgba(0, 0, 0, 0.1);
    position: absolute;
    top: 41%;
    transform: translateY(50%);
    border: none;
    background-color: @white;
    z-index: 1;
    outline: none;
  }

  .slick-prev {
    font-size: 0;
    transform: translateX(-55%);

    &::after {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -7.5px;
      margin-left: -7.5px;
      background-image: url("../img/svg/blue-triangle-icon.svg");
      background-size: 15px 15px;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }

  .slick-next {
    font-size: 0;
    transform: translateX(55%);
    right: 0;

    &::after {
      content: "";
      display: block;
      width: 15px;
      height: 15px;
      position: absolute;
      top: 50%;
      left: 50%;
      margin-top: -7.5px;
      margin-left: -7.5px;
      background-image: url("../img/svg/red-triangle-icon.svg");
      background-size: 15px 15px;
      background-position: 0 0;
      background-repeat: no-repeat;
    }
  }
}

.products__item {
  display: flex !important;
  flex-direction: column;
  height: 100%;
  min-height: 627px;
  border-right: 1px solid #DCDDE6;
  transition: all ease 0.3s;
  position: relative;
  z-index: 1;

  &:hover {
    position: relative;
    z-index: 2;
    box-shadow: 0px 10px 13px 0px rgba(15, 29, 55, 0.11);

  }
}

.products__item--new {
  .products__img-wrapper {
    position: relative;

    &::after {
      content: "Hовинка";
      font-size: 14px;
      line-height: 19px;
      background: #C95A60;
      color: @white;
      padding: 3px 10px;
      position: absolute;
      bottom: -11px;
      left: 20px;
    }
  }
}

.products__img-wrapper {
  height: 255px;
  padding-left: 56px;
  padding-right: 56px;
  padding-top: 23px;
  padding-bottom: 45px;
  text-align: center;
}

.products__txt {
  padding-top: 32px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 35px;
  background-color: @grey;
  flex-grow: 1;
  display: flex;
  flex-direction: column;
}

.products__link {
  font-size: 20px;
  line-height: 24px;
  font-family: @mont;
  font-weight: 600;
  margin-bottom: 15px;
  color: @black;
}

.products__dscr {
  line-height: 19px;
}

.products__price {
  margin-top: auto;
  font-family: @mont;
  font-size: 30px;
  line-height: 30px;
  font-weight: 700;
  color: @black;
  margin-bottom: 25px;


  &::after {
    content: " \20BD";
  }
}

.products__buy {
  border: none;
}
