.footer {
  background-color: @dark-bg;
}

.footer__wrapper {
  padding-top: 50px;
  padding-bottom: 55px;
  display: flex;

  @media screen and (max-width: @medium) {
    flex-wrap: wrap;
  }
}

.footer__column {
  margin-right: 150px;
  width: 100%;
  max-width: 240px;
}

.footer__tel {
  margin-bottom: 27px;
}

.footer__tel-span {
  display: block;
  font-size: 14px;
  line-height: 25px;
  color: @white;
  margin-bottom: 6px;
}

.footer__tel-link {
  font-family: @mont;
  font-weight: 700;
  font-size: 25px;
  line-height: 25px;
  color: @white;
}

.footer__mail {
  margin-bottom: 25px;
}

.footer__mail-span {
  display: block;
  font-size: 14px;
  line-height: 25px;
  color: @white;
  margin-bottom: 5px;
}

.footer__mail-link {
  font-family: @mont;
  font-weight: 700;
  font-size: 25px;
  line-height: 25px;
  color: @white;
}

.footer__btn {
  text-align: center;
  padding-top: 14px;
  padding-bottom: 14px;
  padding-left: 55px;
  padding-right: 55px;
}

.footer__wrapper-menus {
  display: flex;
}

.footer__nav {
  margin-right: 85px;
}

.footer__nav,
.footer__menu {
  padding-top: 25px;
}

.footer__nav-item,
.footer__menu-item {
  margin-bottom: 32px;

}

.footer__nav-link,
.footer__menu-link {
  font-family: @mont;
  font-size: 14px;
  line-height: 1;
  color: @white;
}

.footer__wrapper-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: flex-end;
  flex-grow: 1;
  padding-top: 20px;
  padding-bottom: 30px;
  padding-right: 25px;

  @media screen and (max-width: @medium) {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
  }
}

.footer__priv-pol {
  width: 100%;
  max-width: 260px;
  font-size: 14px;
  line-height: 14px;
  font-family: @mont;
  font-weight: 600;
  color: @white;

  @media screen and (max-width: @medium) {
    margin-right: 19%;
  }
}

.footer__priv-pol-info {
  width: 100%;
  max-width: 260px;
}
