.mobile-menu {
  display: none;

  @media screen and (max-width: @small) {
    display: block;
    width: 100%;
    height: 62px;
    background-color: @dark-bg;
    position: fixed;
    left: 0;
    bottom: 0;
    z-index: 20;
  }
}

.mobile-menu__list {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
