.header__top {
  display: flex;
  justify-content: flex-start;
  align-items: flex-start;
  align-content: flex-start;
  min-height: 68px;
}

.header__mudium-icon {
  display: none;

  @media screen and (max-width: @medium) {
    display: block;
  }
}

.header__logo {
  margin-right: 13.5%;
  margin-top: 6px;

  @media screen and (max-width: @medium) {
    margin-right: 55px;
  }
}

.header__top-user {
  display: flex;
  min-height: 20px;
  margin-top: 22px;
}

.header__time {
  display: block;
  padding-top: 5px;
  padding-left: 30px;
  position: relative;
  padding-right: 40px;
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  margin-right: 3px;

  &::after {
    content: "";
    display: block;
    width: 20px;
    height: 20px;
    background-image: url("../img/clock-icon.png");
    background-position: 0 0;
    background-size: 20px 20px;
    background-repeat: no-repeat;
    position: absolute;
    top: 3px;
    left: 0;
  }
}

.header__tel {
  padding-right: 40px;
}

.header__mail {
  padding-right: 40px;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 1px;
    height: 20px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background-color: #868794;
  }
}

.header__mail-link {
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  color: @text-color;

  @media screen and (max-width: @medium) {
    font-size: 0;
    .header__tel-mudium-icon {

    }
  }
}

.header__search {
  padding-left: 35px;
  padding-right: 25px;
  height: 25px;
  position: relative;

  @media screen and (max-width: @medium) {
    height: 34px;
  }

  &::after {
    content: "";
    display: block;
    width: 1px;
    height: 20px;
    position: absolute;
    top: 50%;
    right: 0;
    transform: translateY(-50%);
    background-color: #868794;
  }

  @media screen and (max-width: @medium) {
    .header__tel-mudium-icon {
      display: block;
    }
  }
}

.header__search-form {
  width: 115px;
  position: relative;

  @media screen and (max-width: @medium) {
    display: none;
  }
}

.header__seacrh-input {
  width: 85px;
  padding-left: 25px;
  border: none;

  &::placeholder {
    font-family: @mont;
    font-size: 14px;
    line-height: 17px;
    font-weight: 600;
    color: @text-color;
  }
}

.header__search-submit {
  display: block;
  width: 25px;
  height: 25px;
  background-image: url("../img/search-icon.png");
  background-position: 0 0;
  background-size: 25px 25px;
  background-repeat: no-repeat;
  position: absolute;
  top: -3px;
  left: 0;
  border: none;
  padding: 0;
  background-color: transparent;
}

.header__cart {
  padding-left: 40px;
  width: 62px;
  height: 20px;
  position: relative;

  @media screen and (max-width: @medium) {
    height: 34px;
  }
}

.header__cart-count {
  position: absolute;
  top: -5px;
  right: -8px;
  font-family: @mont;
  font-size: 11px;
  line-height: 13px;
  font-weight: 600;
  color: @white;
  background-color: @blue;
  padding: 2px 4px;
  border-radius: 50%;
}

.header__cart-link {
  display: block;
  width: 20px;
  height: 20px;
  background-image: url("../img/svg/cart-icon.svg");
  background-position: 0 0;
  background-size: 20px 20px;
  background-repeat: no-repeat;
}

.header__login {
  margin-left: auto;
  margin-top: 12px;
}

.header__login-btn {
  width: 112px;
  text-align: center;
  padding: 12px 0;
}

.header__tel-link {
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  color: @black;

  @media screen and (max-width: @medium) {
    font-size: 0;
    .header__tel-mudium-icon {

    }
  }
}

.header__catalog-btn {
  position: relative;
  padding-left: 45px;
  padding-top: 14px;
  padding-bottom: 13px;
  padding-right: 116px;
  background: #263859;
  border: none;
  color: @white;
  font-family: @mont;
  font-size: 14px;
  line-height: 17px;
  font-weight: 600;
  outline: none;

  @media screen and (max-width: @medium) {
    padding-right: 10px;
  }
}

.header__burger {
  display: block;
  height: 15px;
  width: 16px;
  position: absolute;
  background-color: transparent;
  border: none;
  outline: none;
  top: 17px;
  left: 20px;
  background-image: url(../img/burger-icon.png);
  background-size: cover;
  background-position: 50%;
  background-repeat: no-repeat;
}

.header__bottom {
  min-height: 44px;
  background-color: @dark-bg;

  @media screen and (max-width: @small) {
    display: none;
  }
}

.header__bottom-wrapper {
  display: flex;
  align-items: center;
  min-height: 44px;
}

.header__catalog-wrapper {
  position: relative;
}

.header__catalog-list {
  position: absolute;
  padding-top: 35px;
  background-color: #F4F7FB;
  min-height: 515px;
}

.header__catalog-item {
  padding-left: 43px;
  padding-right: 45px;
  margin-bottom: 24px;
}

.header__catalog-link {
  color: @black;
  font-size: 14px;
  line-height: 17px;
  font-family: @mont;
  font-weight: 600;
}

.header__nav {
  padding-left: 50px;

  @media screen and (max-width: @medium) {
    padding-left: 40px;
  }
}

.header__nav-list {
  display: flex;
  width: 100%;
}

.header__nav-item {
  margin-right: 100px;

  @media screen and (max-width: @medium) {
    margin-right: 10px;
  }

  .header__nav-link {
    color: #C3C8D2;
  }
}

.header__nav-item--active {
  .header__nav-link {
    color: @white;
  }
}

.header__nav-link {
  font-size: 14px;
  line-height: 17px;
  font-family: @mont;
  font-weight: 600;
}

.header__rev-tel {
  margin-left: auto;
}

.header__rev-tel-btn {
  color: @white;
  font-size: 14px;
  line-height: 17px;
  font-family: @mont;
  font-weight: 600;
  padding-left: 30px;
  background-color: transparent;
  border: none;
  position: relative;

  &::after {
    content: "";
    display: block;
    width: 19px;
    height: 19px;
    position: absolute;
    top: 0;
    left: 0;
    background-image: url("../img/svg/tel-icon.svg");
    background-size: 19px 19px;
    background-position: 0 0;
    background-repeat: no-repeat;
  }
}
